.container {
  width: 100%;
  padding: 15px;
  margin: 0 auto;
}

.formSearch {
  text-align: center;
}
.formSearch__style {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  position: fixed;
  background-color: #9f32a5;
  box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 1px rgb(0 0 0 / 14%), 0 2px 1px rgb(0 0 0 / 20%);
}
.search-form-input {
  border-radius: 4px 0 0 4px;
  border: none;
  outline: none;
  background-color: #fff;
  padding: 8px 10px;
  font: inherit;
}
.search-form-button,
.showMore {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  justify-content: center;
  padding: 10px 32px;

  border: none;
  background-color: rgb(200, 200, 200);
  border-radius: 0 4px 4px 0;
  cursor: pointer;
}
.showMore {
  margin-top: 20px;
}
.gallery,
.info,
.photo-card {
  display: flex;
  flex-wrap: wrap;
}
.gallery {
  margin-left: -20px;
  padding-top: 60px;
  justify-content: center;
}
.gallery__link {
  margin-top: 20px;
  margin-left: 20px;
  min-width: 320px;
  flex-basis: calc((100% - 80px) / 4);
  border: 2px solid rgb(241, 238, 238);
  border-radius: 10px;
  overflow: hidden;
  transition: all 0.3s;
}

.gallery__link:hover,
.gallery__link:focus {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}

.gallery-item__img {
  display: block;
  height: 30vh;
  object-fit: cover;
}
.info {
  min-width: 320px;
  flex-basis: calc((100% - 80px) / 4);
}
.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
}
.info-item:not(:first-child) {
  margin-left: 5px;
}
.button.is-hidden {
  display: none;
}
@keyframes spinner-border {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}
